* {
    margin: 0;
    padding: 0;
  }
  
  html {
    width: 80%;
    max-width: 800px;
    margin: 0 auto;
  }
  
  table * { border: none; }
  
  p, ul, ol, blockquote {
    margin-bottom: 1rem !important;
  }
  
  li {
    margin-left: 3rem;
  }
  
  pre {
    padding: 8px !important;
    border: 1px solid #efefef !important;
    border-radius: 4px !important;
    background: transparent !important;
  }
  
  code {
    padding: 4px;
    border-radius: 4px;
    background: #fafafa;
  }
  
  pre>code {
    background: transparent;
  }
  
  .header {
    margin-top: 20px;
  }

  .components {
    margin-bottom: 40px;
  }
  
  .components>.component {
    display: inline-block;
    padding: 0 15px;
    margin: 5px 10px;
    height: 30px;
    line-height: 30px;
    border-radius: 15px;
  }
  
  .incidents {}
  .incidents>.loading {}
  
  .incident {
    margin-bottom: 40px;
    border-bottom: 1px solid #fafafa;
  }
  
  .incident>.title {
    display: inline-block;
    margin-right: 16px;
  }

  .incident>.affected>.component {
    display: inline-block;
    padding: 0 10px;
    margin: 0 5px;
    height: 20px;
    line-height: 20px;
    border-radius: 10px;
  }
  
  .incident>.body {
    margin-bottom: 10px;
    padding-top: 10px;
  }
  .incident>.body>*:last-child {
    margin-bottom: 1rem;
  }
  
  .incident>.updates {}
  .incident>.updates *:first-child { margin-top: 0 !important; }
  .incident>.updates *:last-child { margin-bottom: 0 !important; }
  .incident>.updates>.update {
    margin-bottom: 10px;
    padding-bottom: 10px;
    border-bottom: 1px solid #fafafa;
  }
  .incident>.updates>.update>.body {
    margin-top: 8px;
  }
  .incident.updates>.update>.body>:first-child {
    margin-top: 0;
    margin-bottom: 0;
  }